.std-acc__item {
  border-top: 1px solid #cbcbcb;
  padding: 20px 0;
}

.std-acc__item:first-child {
  border-top: none;
  padding-top: 0;
}

.std-acc__item:last-child {
  padding-bottom: 0;
}

.std-acc__item.is-open .std-acc__arrow {
  top: calc(50% + 3px);
  transform: translateY(-50%)rotate(225deg);
}

.std-acc__item.is-open .std-acc__body {
  height: auto;
}

.std-acc__header {
  cursor: pointer;
  padding-left: 35px;
  position: relative;
}

.std-acc__arrow {
  width: 10px;
  height: 10px;
  border: 2px solid #000;
  border-width: 0 2px 2px 0;
  position: absolute;
  top: calc(50% - 3px);
  left: 2px;
  transform: translateY(-50%)rotate(45deg);
}

.std-acc__title {
  color: inherit;
  margin: 0;
  font-weight: normal;
}

.std-acc__body {
  height: 0;
  transition: height var(--transition-duration, .3s) var(--transition-timing-function, ease-out);
  padding-left: 35px;
  overflow: hidden;
}

.std-acc__inner {
  padding-top: 20px;
}

